import React from 'react';
import spinner from './Spinner.png';

class SearchPage extends React.Component{
    open=()=>{
        document.querySelector('.search').classList.toggle('open');
    }
    up=()=>{
        if (document.querySelector('.search-box').value===''){
            document.querySelector('#mains').setAttribute('class','middle');
            this.props.trig();
        }else {
            document.querySelector('#mains').setAttribute('class','startf');
        }
    }
    render(){
        return(
            <div className="middle" id="mains">
                <div className="search">
                    <input type="search" className="search-box" onChange={this.up} onKeyPress={this.props.result} />
                    <span className="search-button" onClick={this.open}>
                        <span className="search-icon"></span>
                    </span>
                </div>
            </div>
        );
    }
}

class Github extends React.Component{
    render(){
        if (this.props.display){
            if (this.props.loading){
                return(
                    <div id="waiting">
                        <img src={spinner} alt="loading" id="spin" />
                    </div>
                );
            } else if(this.props.alert==='Not Found'){
                return(
                    <div id="waiting">
                        <img src={spinner} alt="loading" id="spin" />
                        <h1>Not Found</h1>
                    </div>
                );
            }
            else {
                return(
                    <div className="row data">
                        <div className="col-md-4">
                            <img className="card-img-top img-fluid rounded shadow-lg" src={this.props.image_src} alt={this.props.name} />
                        </div>
                        <div className="col-md-8">
                            <div className="card shadow-lg">
                                <div className="card-header">
                                    <h1>{this.props.name}</h1>
                                </div>
                                <div className="card-body">
                                    <a className="card-title" href={this.props.his_link} >{`you can find this user on ${this.props.his_link}`}</a>
                                    <p className="card-text">{`Number of Public Repo is ${this.props.repo}`}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                );
            }
        } else {
            return(
                <span></span>
            );
        }
    }
}

class App extends React.Component{
    constructor(){
        super()
        this.state={
            display:false,
            loading:true,
            userdata:'',
            Error:''
        }
    }
    getData=(event)=>{
        if (event.key === 'Enter'){
            this.setState({display:true});
            if (event.target.value.length>=2){
                fetch(`https://api.github.com/users/${event.target.value}`)
                    .then(response=>response.json())
                    .then(user=>this.setState({userdata:user,loading:false}))
                    .catch(err=>this.setState({Error:err}));
            }
        }
    }
    changeState=()=>{
        this.setState({loading:true,display:false});
    }
    render(){
        const {userdata} = this.state;
        return(
            <div>
                <SearchPage result={this.getData} trig={this.changeState} />
                <Github 
                    alert={userdata.message}
                    display={this.state.display}
                    loading={this.state.loading}
                    name={userdata.login} 
                    image_src={userdata.avatar_url} 
                    his_link={userdata.html_url}
                    repo={userdata.public_repos}
                />
            </div>
        );
    }
}
export default App;